.header {
    background-color: darkorange;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.headerContainer {
    box-sizing: border-box;
    border: 1px solid;
    width: 80%;
    height: 80px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}